@use '../base/variables' as v;

*{
    border: red;
}
.dark-mode{
    background-color: v.$black;
    color: v.$blanco;
    .galeria{
        .contenido-galeria{
            box-shadow: 0px 0px 59px 27px rgba(0,0,0,0.75);
            
    
        }
    }
    .socialContainer2{
        border: 2px solid v.$blanco;
        background-color: rgb(44, 44, 44);
    }
   .descripcion{
        .stack{
            display: flex;
            flex-direction: column;
            row-gap: 10px;
            text-align: center;
        }
        .stack>div{
            align-items: center;
            display: flex;
            flex-direction: row;
            gap: 5px;
        }
        .stack>div>p{
            letter-spacing: 3px;
            border: none !important;
        }
        .stack>div>p:nth-child(1){
            background-color: rgb(232, 99, 57);
            padding: .3rem;
            border-radius: 15px;
            text-align: center;
            
        }
        .stack>div>p:nth-child(2){
            background-color: v.$blue;
            padding: .3rem;
            border-radius: 15px;
        }
        
        .stack>div>p:nth-child(3){
            background-color: rgb(194, 96, 158);
            padding: .3rem;
            border-radius: 15px;
        }
        .stack>div>p:nth-child(4){
            background-color: v.$blueClaro;
            padding: .3rem;
            border-radius: 15px;
        }
        .stack>div>p:nth-child(5){
            background-color: rgb(221, 192, 27);
            padding: .3rem;
            border-radius: 15px;
        }
    
        .stack>div>p:nth-child(6){
            background-color: rgb( 19, 153, 196);
            padding: .3rem;
            border-radius: 15px;
        }
        .stack> .backend{
            margin-top: 5px;
        }
        .stack>.backend>p:nth-child(1){
            background-color: rgb(115, 119, 173);
            padding: .3rem;
            border-radius: 15px;
        }
        .stack>.backend>p:nth-child(2){
            background: rgb(39,39,195);
            padding: .3rem;
            border-radius: 15px;
            
        }
        .stack>.backend>p:nth-child(3){
            background: rgb(150, 77, 146);
            padding: .3rem;
            border-radius: 15px;
            
        }
        .stack>.backend>p:nth-child(4){
            background: rgb(77, 150, 79);
            padding: .3rem;
            border-radius: 15px;
            
        }
        .stack>.backend>p:nth-child(5){
            background: rgb(77, 150, 79);
            padding: .3rem;
            border-radius: 15px;
            
        }
   }
  
    
}
