@use '../base/variables' as v;
@use '../base/mixins' as m;
.sitios{
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    
    //titulos de la pagina de sitios
    .titulo-html-css{
        text-align: center;
        margin-top: 10px;
        letter-spacing: 3px;
    }
    .titulo-fullstack{
        text-align: center;
        margin-top: 25px;
        letter-spacing: 3px;
        .descripcion-fullstack{
            color: v.$blue;
        }
    }
   
    .titulo-jsmoderno{
        text-align: center;
        letter-spacing: 3px;
    }
    
    .galeria-sitios{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        justify-items: center;
        margin-top: 30px;
        gap: 25px;
        
        .card_back{
            display: flex;
            position: absolute;
            flex-direction: row;
            gap: 25px;
            margin-top: 10px;
            justify-content: center;
            align-items: center;
            color: #fff;
            
            border-radius: 15px;
            width: 320px;
            height: 200px;
            z-index: -1;
            
          .tecnologias{
            width: 70px;
            display: flex;
            margin: 5px auto;
            
            
          }
          .tecnologias-css{
            width: 50px;
            display: flex;
            margin: 5px auto;
            
            
          }
          .tecnologias-sass{
            width: 70px;
            display: flex;
            
            margin: 10px auto;
            background-size: cover;
          }
            
        }
        .card_back:hover{
            display: flex;
            flex-direction: column;
            
            background-color: #343434e0;
            
            z-index: 999;
        }
        .card_back>div{
            width: 80px;
            height: 80px;
           
        }
        .picture{
            height: 200px;
            border-radius: 15px;
         }
        
         .imagen{
            
             width: 320px;
             height: 100%;
             border-radius: 15px;
            
         }
         
         
         .imagen:hover{
            opacity: 0.1;
            z-index: -999;
            background: #939393;
            animation: cardAnimacion 1.4s;
            
         }
         
     }
     //contenedor de la imagen de viajes
    .proyectos-fullstack{
    
        max-width: 800px;
        margin: 0 auto;

    .proyecto-viaje{
        width: 100%;
        border-radius: 15px;
        margin: 1rem auto;
        
    }
    }
}
@include m.desktop{
    .sitios{
        .galeria-sitios{
            grid-template-columns: repeat(2, 1fr);
           
        }

    }
}
@include m.tablet{
    .sitios{
        .galeria-sitios{
            grid-template-columns: repeat(2, 1fr);
           
        }

    }
}
@include m.celular{
    
    .sitios{
        
        .galeria-sitios{
            display: flex;
            flex-direction: column;
            align-items: center;
        }
        .galeria-jsmoderno{
            margin-left: 10px;
            text-align: justify;
        }
    }
}
