@use '../base/variables' as v;
@use '../base/mixins' as m;
.footer{
    display: flex;
    flex-direction: column;
    

    min-height: 3.4rem;
    width: 100%;
    background-color: v.$grisTansparente;
    text-align: center;
    color: v.$blanco;
   
    padding: 1rem 1rem  1rem 1rem;
    
    .contactos{
        
        display: flex;
        flex-direction: column;
        margin-top: 10px;

        flex: 1 1 auto;
    }
    .footer-container{
        flex: 1 1 auto;
        margin-top: 25px;
    }
    .card{
        justify-content: end;
    }
    
}
@include m.celular{
    .footer{
        
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    
}