@use '../base/variables' as v;
@use '../base/mixins' as m;

.seccion1{
    display: flex;
    flex-direction: row;
    gap: 2.5rem;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    text-shadow: 0px 6px 8px rgba(0, 0, 0, 0.3);
    margin: 20px 25px 0 25px;

    .galeria{
    .contenido-galeria{
        
       
        max-width: 700px;
        height: 300px;
        
        border-radius: 20px;
        z-index: 10;
        box-shadow: 0px 0px 36px -2px rgba(0,0,0,0.75);
        margin: 0 0;
        
        img{
            height: 100%;
            width: 600px;
            border-radius: 15px;
        }
        
    }
    section{
        margin: 0 0 0 0;
        display: flex;
        flex-direction: column;
        gap: .8rem;
    }

    ///Botones
    .botones-galeria{
        display: flex;
        justify-content: space-around;
        margin-top: 15px;
    
          /* button settings ðŸ‘† */
          .ui-btn {
            --btn-default-bg: rgb(41, 41, 41);
            --btn-padding: 15px 20px;
            --btn-hover-bg: rgb(51, 51, 51);
            --btn-transition: .3s;
            --btn-letter-spacing: .1rem;
            --btn-animation-duration: 1.2s;
            --btn-shadow-color: rgba(0, 0, 0, 0.137);
            --btn-shadow: 0 2px 10px 0 var(--btn-shadow-color);
            --hover-btn-color: #FAC921;
            --default-btn-color: #fff;
            --font-size: 16px;
            /* ðŸ‘† this field should not be empty */
            --font-weight: 600;
            --font-family: Menlo,Roboto Mono,monospace;
            /* ðŸ‘† this field should not be empty */
          }
          
          /* button settings ðŸ‘† */
          
          .ui-btn {
            box-sizing: border-box;
            padding: var(--btn-padding);
            display: flex;
            align-items: center;
            justify-content: center;
            color: var(--default-btn-color);
            font: var(--font-weight) var(--font-size) var(--font-family);
            background: var(--btn-default-bg);
            border: none;
            cursor: pointer;
            transition: var(--btn-transition);
            overflow: hidden;
            box-shadow: var(--btn-shadow);
            border-radius: 10px;
          }
          
          .ui-btn span {
            letter-spacing: var(--btn-letter-spacing);
            transition: var(--btn-transition);
            box-sizing: border-box;
            position: relative;
            background: inherit;
          }
          
          .ui-btn span::before {
            box-sizing: border-box;
            position: absolute;
            content: "";
            background: inherit;
          }
          
          .ui-btn:hover, .ui-btn:focus {
            background: var(--btn-hover-bg);
          }
          
          .ui-btn:hover span, .ui-btn:focus span {
            color: v.$blue;
          }
          
          .ui-btn:hover span::before, .ui-btn:focus span::before {
            animation: chitchat linear both var(--btn-animation-duration);
          }
          
         
       
    }

    
    

}

//---------Informacion
.mi-informacion{
    display: flex;
    .descripcion{
        display: flex;
        flex-direction: column;
        row-gap: 15px;
        align-items: center;
        text-shadow: none;
        .stack{
            display: flex;
            flex-direction: column;
            row-gap: 10px;
            text-align: center;
        }
        .stack>div{
            align-items: center;
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            gap: 5px;
            
            
        }
        .stack>div>p{
            border: 2px solid v.$grisOscuro;
            padding: .5rem;
            border-radius: 15px;
            letter-spacing: 3px;
        }
        .foto-presentacion{
           box-shadow: -1px 1px 75px 5px v.$blueClaro;
            width: 160px;
            height: 160px;
            padding: 5px;
            border-radius: 50%;
            border: v.$blueClaro  5px solid;
        }
        
    }
    .descripcion>p>span{
        color: v.$blue;
    }
    .informacion{
        display: grid;
        justify-items: center;
        /*grid-template-columns: repeat(2, 1fr);*/
        gap: 1rem;
        max-width: 500px;
        
        margin-top: 30px;
        
        
        
    }
    
    }
}
@include m.desktop{
    .seccion1{
        display: flex;
        flex-direction: column;

    }
    .mi-informacion{
        .descripcion{
            flex-direction: row;
           
        }
    }
}  
@include m.tablet{
    .seccion1{
        display: flex;
        flex-direction: column;
    }
    .secciones{
        display: flex;
        flex-direction: column;
        .mi-informacion{
            .descripcion{
                text-align: justify;
                padding: .3rem;
                
            }
            
        }
    }
    .mi-informacion{
        .descripcion{
            flex-direction: row;
        }
    }
}
@include m.celular{
    
    .secciones{
        display: flex;
        flex-direction: column;
        margin: 10px 0 0 0;
        .mi-informacion{
            display: flex;
            .informacion{
                margin: 5px;
                
                .stack > .frontend{
                    display: grid;
                    grid-template-columns: repeat(4, 1fr);
                }
                .stack > .backend{
                    display: grid;
                    grid-template-columns: repeat(3, 1fr);
                }
               
            }
        }
        .galeria{
            
            .contenido-galeria{
                
                .slide{
                    width: 443px;
                }
            }
        }
        .skills{
            #frontend{
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                
            }
            #backend{
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                
            }
        }
    }
}
