@use '../base/variables' as v;
@mixin heading($color: red, $border-radius: false) {//una pieza de codigo que se puede reutilizar en multiples lugares en la hoja de estilos
    text-transform: uppercase;
    font-size: 2.4rem;
    color: $color;
    letter-spacing: 5px;
    
    @if $border-radius{
        border-radius: 1rem;
    }

} 
//usar min-width cuando el diseÃ±o inicia desde celular
//usar max-width cuando inicio desde la pantallagrande
@mixin celular{
    @media(max-width: v.$celular){
        @content;//lo que hac es que todo lo que agreguemos en cuando lo llamemos en nueestro codigo se agregara como parametro todo lo que le agreguemos
    }
}

@mixin tablet{
    @media(max-width: v.$tablet){
        @content;//lo que hac es que todo lo que agreguemos en cuando lo llamemos en nueestro codigo se agregara como parametro todo lo que le agreguemos
    }
}
@mixin desktop{
    @media(max-width: v.$desktop){
        @content;//lo que hac es que todo lo que agreguemos en cuando lo llamemos en nueestro codigo se agregara como parametro todo lo que le agreguemos
    }
}
@mixin desktopxl{
    @media(max-width: v.$desktopxl){
        @content;//lo que hac es que todo lo que agreguemos en cuando lo llamemos en nueestro codigo se agregara como parametro todo lo que le agreguemos
    }
}
@mixin grid($colum, $espaciado){
    display: grid;
    grid-template-columns: repeat($colum, 1fr);
    gap: $espaciado;

}
@mixin resetear-lista{
    padding: 0;
    margin: 0;
    list-style: none;
}