body {
  text-decoration: none;
}
body .titulo-portafolio {
  margin-top: 1.8rem;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 3px;
}
body p {
  line-height: 25px;
}

img {
  object-fit: cover;
