.seccion1 {
  display: flex;
  flex-direction: row;
  gap: 2.5rem;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  text-shadow: 0px 6px 8px rgba(0, 0, 0, 0.3);
  margin: 20px 25px 0 25px;
}
.seccion1 .galeria .contenido-galeria {
  max-width: 700px;
  height: 300px;
  border-radius: 20px;
  z-index: 10;
  box-shadow: 0px 0px 36px -2px rgba(0, 0, 0, 0.75);
  margin: 0 0;
}
.seccion1 .galeria .contenido-galeria img {
  height: 100%;
  width: 600px;
  border-radius: 15px;
}
.seccion1 .galeria section {
  margin: 0 0 0 0;
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}
.seccion1 .galeria .botones-galeria {
  display: flex;
  justify-content: space-around;
  margin-top: 15px;
  /* button settings ðŸ‘† */
  /* button settings ðŸ‘† */
}
.seccion1 .galeria .botones-galeria .ui-btn {
  --btn-default-bg: rgb(41, 41, 41);
  --btn-padding: 15px 20px;
  --btn-hover-bg: rgb(51, 51, 51);
  --btn-transition: .3s;
  --btn-letter-spacing: .1rem;
  --btn-animation-duration: 1.2s;
  --btn-shadow-color: rgba(0, 0, 0, 0.137);
  --btn-shadow: 0 2px 10px 0 var(--btn-shadow-color);
  --hover-btn-color: #FAC921;
  --default-btn-color: #fff;
  --font-size: 16px;
  /* ðŸ‘† this field should not be empty */
  --font-weight: 600;
  --font-family: Menlo,Roboto Mono,monospace;
  /* ðŸ‘† this field should not be empty */
}
.seccion1 .galeria .botones-galeria .ui-btn {
  box-sizing: border-box;
  padding: var(--btn-padding);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--default-btn-color);
  font: var(--font-weight) var(--font-size) var(--font-family);
  background: var(--btn-default-bg);
  border: none;
  cursor: pointer;
  transition: var(--btn-transition);
  overflow: hidden;
  box-shadow: var(--btn-shadow);
  border-radius: 10px;
}
.seccion1 .galeria .botones-galeria .ui-btn span {
  letter-spacing: var(--btn-letter-spacing);
  transition: var(--btn-transition);
  box-sizing: border-box;
  position: relative;
  background: inherit;
}
.seccion1 .galeria .botones-galeria .ui-btn span::before {
  box-sizing: border-box;
  position: absolute;
  content: "";
  background: inherit;
}
.seccion1 .galeria .botones-galeria .ui-btn:hover, .seccion1 .galeria .botones-galeria .ui-btn:focus {
  background: var(--btn-hover-bg);
}
.seccion1 .galeria .botones-galeria .ui-btn:hover span, .seccion1 .galeria .botones-galeria .ui-btn:focus span {
  color: rgb(24, 104, 253);
}
.seccion1 .galeria .botones-galeria .ui-btn:hover span::before, .seccion1 .galeria .botones-galeria .ui-btn:focus span::before {
  animation: chitchat linear both var(--btn-animation-duration);
}
.seccion1 .mi-informacion {
  display: flex;
}
.seccion1 .mi-informacion .descripcion {
  display: flex;
  flex-direction: column;
  row-gap: 15px;
  align-items: center;
  text-shadow: none;
}
.seccion1 .mi-informacion .descripcion .stack {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  text-align: center;
}
.seccion1 .mi-informacion .descripcion .stack > div {
  align-items: center;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 5px;
}
.seccion1 .mi-informacion .descripcion .stack > div > p {
  border: 2px solid #847577;
  padding: 0.5rem;
  border-radius: 15px;
  letter-spacing: 3px;
}
.seccion1 .mi-informacion .descripcion .foto-presentacion {
  box-shadow: -1px 1px 75px 5px rgba(24, 104, 253, 0.504);
  width: 160px;
  height: 160px;
  padding: 5px;
  border-radius: 50%;
  border: rgba(24, 104, 253, 0.504) 5px solid;
}
.seccion1 .mi-informacion .descripcion > p > span {
  color: rgb(24, 104, 253);
}
.seccion1 .mi-informacion .informacion {
  display: grid;
  justify-items: center;
  /*grid-template-columns: repeat(2, 1fr);*/
  gap: 1rem;
  max-width: 500px;
  margin-top: 30px;
}

@media (max-width: 992px) {
  .seccion1 {
    display: flex;
    flex-direction: column;
  }
  .mi-informacion .descripcion {
    flex-direction: row;
  }
}
@media (max-width: 768px) {
  .seccion1 {
    display: flex;
    flex-direction: column;
  }
  .secciones {
    display: flex;
    flex-direction: column;
  }
  .secciones .mi-informacion .descripcion {
    text-align: justify;
    padding: 0.3rem;
  }
  .mi-informacion .descripcion {
    flex-direction: row;
  }
}
@media (max-width: 480px) {
  .secciones {
    display: flex;
    flex-direction: column;
    margin: 10px 0 0 0;
  }
  .secciones .mi-informacion {
    display: flex;
  }
  .secciones .mi-informacion .informacion {
    margin: 5px;
  }
  .secciones .mi-informacion .informacion .stack > .frontend {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }
  .secciones .mi-informacion .informacion .stack > .backend {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
  .secciones .galeria .contenido-galeria .slide {
    width: 443px;
  }
  .secciones .skills #frontend {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  .secciones .skills #backend {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
