.card {
  padding: 0 25px;
  background-color: none;
  display: flex;
  margin-top: 15px;
  justify-content: center;
  gap: 20px;
}

/* for all social containers*/
.socialContainer {
  width: 52px;
  height: 52px;
  background-color: rgb(44, 44, 44);
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  transition-duration: 0.3s;
}

.socialContainer2 {
  width: 35px;
  height: 35px;
  border: 2px solid #A6A2A2;
  background-color: rgb(241, 241, 241);
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  transition-duration: 0.3s;
}

/* instagram*/
.containerOne:hover {
  background-color: #d62976;
  transition-duration: 0.3s;
}

/* twitter*/
.containerTwo:hover {
  background-color: #00acee;
  transition-duration: 0.3s;
}

/* linkdin*/
.containerThree:hover {
  background-color: #0072b1;
  transition-duration: 0.3s;
}

/* Whatsapp*/
.containerFour:hover {
  background-color: #128C7E;
  transition-duration: 0.3s;
}

.socialContainer:active {
  transform: scale(0.9);
  transition-duration: 0.3s;
}

.socialContainer2:active {
  transform: scale(0.9);
  transition-duration: 0.3s;
}

.socialSvg {
  width: 17px;
}

.socialSvg path {
  fill: rgb(255, 255, 255);
}

.socialContainer:hover .socialSvg {
  animation: slide-in-top 0.3s both;
}

.socialContainer2:hover .socialSvg {
  animation: slide-in-top 0.3s both;
}

@keyframes slide-in-top {
  0% {
    transform: translateY(-50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
.footer {
  display: flex;
  flex-direction: column;
  min-height: 3.4rem;
  width: 100%;
  background-color: rgba(32, 35, 37, 0.919);
  text-align: center;
  color: rgb(241, 241, 241);
  padding: 1rem 1rem 1rem 1rem;
}
.footer .contactos {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  flex: 1 1 auto;
}
.footer .footer-container {
  flex: 1 1 auto;
  margin-top: 25px;
}
.footer .card {
  justify-content: end;
}

@media (max-width: 480px) {
  .footer {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
@keyframes chitchat {
  0% {
    content: "#";
  }
  5% {
    content: "&@$";
  }
  10% {
    content: "^{-^";
  }
  15% {
    content: "-3{!";
  }
  20% {
    content: "#$_!#";
  }
  25% {
    content: "â„–:0$!";
  }
  30% {
    content: "#}+.";
  }
  35% {
    content: "@}-?";
  }
  40% {
    content: "?{4@%";
  }
  45% {
    content: "=.,^!";
  }
  50% {
    content: "?2@%";
  }
  55% {
    content: ";1}]";
  }
  60% {
    content: "?{%:%";
    right: 0;
  }
  65% {
    content: "|{f[4";
    right: 0;
  }
  70% {
    content: "{4%0%";
    right: 0;
  }
  75% {
    content: "'1_0<";
    right: 0;
  }
  80% {
    content: "{0%";
    right: 0;
  }
  85% {
    content: "]>'";
    right: 0;
  }
  90% {
    content: "4";
    right: 0;
  }
  95% {
    content: "2";
    right: 0;
  }
  100% {
    content: "";
    right: 0;
  }
}
