}
.nav .toggle-switch {
  position: relative;
  width: 80px;
  height: 50px;
  margin: 10px 0 0 10px;
  --light: #d8dbe0;
  --dark: #28292c;
  --link: rgb(27, 129, 112);
  --link-hover: rgb(24, 94, 82);
}
.nav .switch-label {
  position: absolute;
  width: 100%;
  height: 32px;
  background-color: var(--dark);
  border-radius: 25px;
  cursor: pointer;
  border: 3px solid var(--dark);
}
.nav .checkbox {
  position: absolute;
  display: none;
}
.nav .slider {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 25px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.nav .checkbox:checked ~ .slider {
  background-color: var(--light);
}
.nav .slider::before {
  content: "";
  position: absolute;
  top: 0;
  left: 10px;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  -webkit-box-shadow: inset 12px -4px 0px 0px var(--light);
  box-shadow: inset 12px -4px 0px 0px var(--light);
  background-color: var(--dark);
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.nav .checkbox:checked ~ .slider::before {
  -webkit-transform: translateX(35px);
  -ms-transform: translateX(35px);
  transform: translateX(35px);
  background-color: var(--dark);
  -webkit-box-shadow: none;
  box-shadow: none;
}

@media (max-width: 480px) {
  .nav {
    display: flex;
    flex-direction: column;
  }
}